import axios from "axios"
import store from "../store"
export default{
    install : function(Vue){
        Vue.prototype.firstOpen = () => {
            if(store.state.login.ifLogin){
				axios.get(store.state.url_api + "insert_analytics_login",{
					headers: {
						"Accept": "application/json",
						"Authorization": "Bearer " + localStorage.token
					}
				})
			}else{
				if(store.state.identifier!=null || store.state.identifier!=""){
					axios.post(store.state.url_api + "insert_analytics_tidak_login",{
						identifier:store.state.identifier
                    })
				}else{
					axios.post(store.state.url_api + "insert_analytics_tidak_login").then(response => {
						var d = new Date();
						d.setTime(d.getTime() + (10*10*365*24*60*60));
						var expires = "expires="+ d.toUTCString();
						document.cookie = "identifier=" + response.data + ";" + expires;
					})
				}
			}
        }
    }
}