<template>
  <div id="app">
    <router-view/>
  </div>
</template>
<script>
export default {
  async created(){
    if(this.$store.state.login.ifLogin){
      this.axios.get(this.$store.state.url_api + 'auth/c', {
        headers : {
          "Authorization": "Bearer " + localStorage.token
        }
      })
    }
    await this.$store.dispatch('setIdentifier');
    // this.firstOpen();
    this.$store.dispatch('loadCart');
  }
}
</script>
<style>
  body{
    background-color: #F2F3F4;
  }
</style>
