import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    component: () => import('../views/Home.vue'),
    children:[
      {
        path: '',
        alias:'c',
        name: 'HomePage',
        component: () => import('../components/Home/Home'),
      },
      {
        path: '/pdam',
        meta: {
          requiredLogin:true
        },
        component: () => import('../components/Product/Pdam'),
      },
      {
        path: 'c/:kategori-:kategori_id',
        name: 'HomeWithProduct',
        component: () => import('../components/Home/HomeWithProduct'),
      }
    ],
  },
  {
    path: '/trio-motor',
    component: () => import('../components/Trio/trioService')
  },
  {
    path: '/account',
    component: () => import('../views/Account.vue'),
    children:[
      {
        path: '',
        redirect: () => {
          if(store.state.login.ifLogin){
            return {name:'profile'}
          }else{
            return {name:'login'}
          }
        }
      },
      {
        path: 'register',
        name: 'register',
        component: () => import('../components/Account/Register'),
        meta: {
          name: "Halaman Pendaftaran"
        }
      },
      {
        path: 'login',
        name: 'login',
        component: () => import('../components/Account/Login'),
        meta: {
          name: "Halaman Login"
        }
      },
      {
        path: 'forgot',
        name: 'forgot',
        component: () => import('../components/Account/Forgot'),
        meta: {
          name: "Lupa Password"
        },
      },
      {
        path: 'profile',
        name: 'profile',
        component: () => import('../components/Account/Profile'),
        meta: {
          name: "Profile Pian"
        }
      }
    ],
    beforeEnter: (to, from, next) => {
      if(store.state.login.ifLogin && to.name!='profile') next({name:'HomePage'})
      else next()
    }
  },
  {
    path: '/failed',
    component: () => import('../components/modal/Failed'),
    meta: {
      name: "Login Gagal"
    }
  },
  {
    path: '/search',
    name:'search',
    component: () => import('../components/Home/Search'),
  },
  {
    path: '/transaction',
    component: () => import('../views/Transaction.vue'),
    meta: {
      requiredLogin:true
    },
    children: [
      {
        path: '',
        redirect: {name:'cart'}
      },
      {
        path: 'cart',
        name: 'cart',
        component: () => import('../components/Transaction/Cart'),
        meta: {
          name: "Keranjang Belanja Pian"
        }
      },
      {
        path: 'address',
        name:'address',
        component: () => import('../components/Transaction/Address/Address'),
        meta: {
          name: "Detail Alamat"
        }
      },
      {
        path: 'whatsapp',
        component: () => import('../components/Account/EditNumber')
      },
      {
        path: 'checkout/:id/confirm',
        name:'checkout',
        component: () => import('../components/Transaction/Checkout'),
        meta: {
          name: "Pembelian Sukses"
        }
      },
      {
        path: 'history',
        name:'history',
        component: () => import('../components/Transaction/History'),
        meta: {
          name: "Riwayat Transaksi"
        }
      },
      {
        path: 'history/map/:transaction_number',
        name:'MapDetails',
        component: () => import('../components/Transaction/MapDetails'),
        meta: {
          name: "Rute Pengiriman",
          cart:false,
          back:false,
          linkBack:'history'
        }
      },
    ]
  },
  {
    path: '/p',
    component: () => import('../views/Product.vue'),
    children:[
      // {
      //   path: ':kategori?-:kategori_id?',
      //   // redirect: '/c/:kategori-:kategori_id',
      //   redirect: (to) => {
      //     return {
      //       name:'HomeWithProduct',
      //       params:{
      //         kategori:to.params.kategori,
      //         kategori_id:to.params.kategori_id
      //       }
      //     }
      //   }
      // },
      {
        name:'Product',
        path: ':kategori-:kategori_id/:product_id-:product',
        component: () => import('../components/Product/Product'),
        meta: {
          name: "Detail Produk"
        }
      },
      {
        name: 'Store',
        path: ':store',
        component: () => import('../components/Product/Store'),
        meta: {
          name: "Semua Produk Toko"
        }
      }
    ]
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if(to.matched.some(record => record.meta.requiredItems)){
    if(Object.prototype.hasOwnProperty.call(to.query, 'items')&&to.query.items&&to.query.items.length>0){
      next()
    }else{
      next({name:'HomePage'});
    }
  }
  if (to.matched.some(record => record.meta.requiredLogin)) {
    if (store.state.login.ifLogin) {
      next()
    }else{
      next({name:'login',query:{from:from.name}})
    }
  }else{
    next()
  } 
})


export default router
