import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)
var host = window.location.hostname=='localhost' ? 'http://127.0.0.1:8000/api/' : 'https://tukarjual.com/TJAPI/public/api/';
// var host = window.location.hostname=='localhost' ? 'http://localhost/TukarJual/TJAPI/public/' : 'https://api.tukarjual.com';
var url_product =window.location.hostname=='localhost' ? 'http://localhost/TukarJual/product/medium_' : 'https://tukarjual.com/product/medium_'
var url_banner =window.location.hostname=='localhost' ? 'http://localhost/TukarJual/images/promo/' : 'https://tukarjual.com/images/promo/'
var url_category =window.location.hostname=='localhost' ? 'http://localhost/TukarJual/images/category/' : 'https://tukarjual.com/images/category/'
var url_images =window.location.hostname=='localhost' ? 'http://localhost/TukarJual/images/' : 'https://tukarjual.com/images/'

const login = {
  state:{
    ifLogin:localStorage.getItem('token') ? true : false,
    identifier:"",
  },

  mutations:{
    SET_IFLOGIN(state, param){
      state.ifLogin = param;
    },
    SET_IDENTIFIER(state, identifier){
      state.identifier = identifier;
    }
  },

  actions:{
    setIfLogin({commit}, param){
      commit('SET_IFLOGIN', param);
    },
    setIdentifier({commit}){
      var name = "identifier=";
      var decodedCookie = decodeURIComponent(document.cookie);
      var ca = decodedCookie.split(';');
      for (var i = 0; i < ca.length; i++) {
        var c = ca[i];
        while (c.charAt(0) == ' ') {
          c = c.substring(1);
        }
        if (c.indexOf(name) == 0) {
          commit('SET_IDENTIFIER', c.substring(name.length, c.length));
        }
      }
    },
  }
}



const category = {
  state: {
    category:null
  },

  mutations:{
    SET_CATEGORY(state, category){
      state.category = category
    }
  },
  actions:{
    set_category({commit}, category){
      commit('SET_CATEGORY', category);
    }
  }
}

const product = {
  state: {
    product:null,
    last_category:null
  },

  getters:{
    product_length(state){
      return state.product!=null ? state.product.length : 0;
    }
  },

  mutations:{
    SET_PRODUCT(state, product){
      if(state.product==null){
        state.product = [];
      }
      product.forEach(p => {
        state.product.push(p);
      })
    },
    RESET_PRODUCT(state){
      state.product = null;
    },
    LAST_CATEGORY(state, category){
      state.last_category = category
    }
  },

  actions:{
    set_product({commit}, product){
      commit('SET_PRODUCT', product);
    },
    reset_product({commit}){
      commit('RESET_PRODUCT');
    },
    set_last_category({commit}, category){
      commit('LAST_CATEGORY', category)
    }
  }
}

const cart = {
  state: {
    cart_product:null,
    cancelToken:null
  },
  getters: {
    cart_product_length: state => {
      return state.cart_product!=null ? state.cart_product.length: 0;
    },

    jumlah_beli_gabungan: (state, getters) => (id_barang, jumlah_beli) => {
      if(jumlah_beli==""){
        jumlah_beli = 0;
      }
      var response = parseInt(jumlah_beli);
      if(getters.cart_product_length!=0){
        if(state.cart_product.find(p => p.id_barang==id_barang)){
          response = parseInt(state.cart_product.find(p => p.id_barang==id_barang).jumlah)+parseInt(jumlah_beli);
        }
      }
      return response;
    }
  },
  mutations: {
    SET_CANCEL_TOKEN(state, param){
      state.cancelToken = param;
    },
    SAVE_CART(state, product){
      state.cart_product = product;
    },
    MUTATIONS_PRODUCT(state,product){
      var p = state.cart_product.findIndex(p => p.id_barang == product.id_barang);
      if(p!=-1){
        state.cart_product[p] = product;
      }else{
        state.cart_product.push(product);
      }
      state.cancelToken = null;
    },
    DELETE_PRODUCT(state,transaction_number){
      var p = state.cart_product.findIndex(p => p.no_transaksi == transaction_number);
      state.cart_product.splice(p, 1);
    }
  },
  actions: {
    loadCart({commit, rootState}) {
      if(rootState.login.ifLogin){
        axios.get(this.state.url_api + "cart", {
          headers: {
            "Accept": "Application/json",
            "Authorization": "Bearer " + localStorage.token
          }
        }).then(response => {
          commit('SAVE_CART', response.data);
        })
      }else{
        commit('SAVE_CART', []);
      }
    },

    addProductToCart({state, commit}, payload){
      if(state.cancelToken!=null){
        state.cancelToken.cancel();
      }
      let cancelToken = axios.CancelToken.source();
      commit('SET_CANCEL_TOKEN', cancelToken);
      axios.post(this.state.url_api + "cart/store", payload, {
        headers:{
          "Accept": "Application/json",
          "Authorization": "Bearer " + localStorage.token
        },
        cancelToken: cancelToken.token
      }).then(response => {
        commit('MUTATIONS_PRODUCT',response.data.item);
      }).catch(e => {
        console.log(e);
      })
    },
  }
}

const transaction = {
  state:{
    items:[],
    addresses:[],
    payments:null
  },

  mutations:{
    SAVE_ITEMS(state, items){
      state.items = items;
    },
    SAVE_ADDRESSES(state, addresses){
      state.addresses = addresses
    },
    SAVE_PAYMENTS(state, payments){
      state.payments = payments
    }
  },

  actions:{
    save_items({commit}, payload){
      commit("SAVE_ITEMS", payload);
    },
    save_addresses({commit}, payload){
      commit('SAVE_ADDRESSES', payload)
    },
    save_payments({commit}, payload){
      commit('SAVE_PAYMENTS', payload)
    }
  }
}

const address = {
  namespaced:true,
  state:{
    desa:'',
    rt:'',
    rw:'',
    kode_pos:'',
    details:'',
    kordinat:null,
    loading:true
  },
  mutations:{
    SAVE_ADDRESS(state, items){
      state.desa = items.desa;
      state.rt = items.rt;
      state.rw = items.rw;
      state.kode_pos = items.kode_pos;
      state.details = items.details;
      state.loading = false
    },
    SAVE_KORDINAT(state, kordinat){
      state.kordinat = kordinat
    }
  },

  actions:{
    save_address({commit}, payload){
      commit("SAVE_ADDRESS", payload);
    },
    save_kordinat({commit}, payload){
      commit("SAVE_KORDINAT", payload)
    }
  }
}

const search = {
  state:{
    product:null
  },

  getters:{
    product_search_length: state => {
      return state.product != null ? state.product.length : 0;
    }
  },

  mutations:{
    SAVE_PRODUCT_SEARCH(state, product){
      if(state.product==null){
        state.product = [];
      }
      product.forEach(p => {
        state.product.push(p)
      })
    },
    RESET_PRODUCT_SEARCH_TO_NULL(state){
      state.product = null;
    },
    RESET_PRODUCT_SEARCH(state){
      state.product = [];
    }
  },

  actions:{
    save_product_search({commit}, payload){
      commit("SAVE_PRODUCT_SEARCH", payload);
    },
    reset_product_search_to_null({commit}){
      commit("RESET_PRODUCT_SEARCH_TO_NULL");
    },
    reset_product_search({commit}){
      commit("RESET_PRODUCT_SEARCH");
    }
  }
}

const location = {
  namespaced: true,
  state:{
    near_location:null
  },

  mutations:{
    SAVE_NEAR_LOCATION(state, loc){
      state.near_location = loc
    }
  },

  actions:{
    save_near_location({commit}, payload){
      commit("SAVE_NEAR_LOCATION", payload);
    }
  }
}


export default new Vuex.Store({
  state: {
    url_api : host,
    url_product : url_product,
    url_banner : url_banner,
    url_category : url_category,
    url_images : url_images,
  },
  modules:{
    login:login,
    cart:cart,
    transaction:transaction,
    address:address,
    search:search,
    product:product,
    category:category,
    location:location
  },
})
